/* base */
html {
  position: relative;
  min-height: 100%;
}

body {
  background-color: $gray-back;
  color: $font-sentense;
  // font-family: 'Noto Sans JP', 'Avenir', 'Helvetica Neue', 'Helvetica', 'Arial', 'Hiragino Sans', 'ãã©ã®ãè§ã´ã·ãã¯', YuGothic, 'Yu Gothic', 'ã¡ã¤ãªãª', Meiryo, 'ï¼­ï¼³ ï¼°ã´ã·ãã¯', 'MS PGothic', sans-serif;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif;
  font-size: 1rem;
  font-weight: 400;
}

html,
body {
  margin: 0;
}

.wrap {
  padding-top: 85px;
  min-height: 100%;
  margin-bottom: -50px;
}

.home .wrap {
  padding-top: 30px;
}

@media only screen and (max-width: 992px) {
  .home .wrap {
    padding-top: 0;
  }
}

@media only screen and (max-width: 992px) {
  .main,
  .sidebar {
    padding: 0;
  }
}

.footer,
.push {
  height: 50px;
}

a {
  color: $main3;
  text-decoration: underline;

  &:hover {
    color: $main3;
    text-decoration: none;
  }
}

h1 {
  color: $font-zero;
}

h2,
h3,
h4,
h5,
h6 {
  color: $font-main;
}

p {
  line-height: 1.7;
}

.jumbotron {
  margin-bottom: 0;
  text-align: center;
  background: url(../images/top-img2.jpg) center center no-repeat #464646;
  -webkit-background-size: cover;
  background-size: cover;
  background-color: $main;

  p {
    font-size: 1rem;
    color: white;
    font-weight: 500;
  }

  img {
    height: 150px;
  }
}

.content {
  margin-bottom: 40px;

  a img:hover {
    box-shadow: 0 4px 8px 2px rgba(0, 0, 0, 0.2);
    -webkit-transform: scale(1.01);
    transform: scale(1.01);
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
  }
}



